import { create, copy, each, isFunction } from 'ntils';

export default function Scope(parent, props) {
  // scope  _observer_ 
  // scope 
  // cteate  _observer_ 
  // scope  defineProperty  parentScope
  let scope = create(parent);
  copy(props, scope);
  // func  scope ;
  each(parent, (key, value) => {
    if (!isFunction(value)) return;
    scope[key] = value.bind(parent);
  });
  return scope;
}


